/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from '@wordpress/i18n';

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-block-editor/#useBlockProps
 */
import { useBlockProps } from '@wordpress/block-editor';

/**
 * The save function defines the way in which the different attributes should
 * be combined into the final markup, which is then serialized by the block
 * editor into `post_content`.
 *
 * @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#save
 *
 * @return {WPElement} Element to render.
 */
export default function save(props) {
    const { attributes } = props;

	return (
		<div className="oet-footnotes-block" { ...useBlockProps.save() }>
            <hr className="wp-block-separator has-text-color has-background has-dark-gray-background-color has-dark-gray-color is-style-wide" />
            { attributes.footnotes.map((item, key)=> ( <div className="oet-footnote-block">
                <div className="wp-block-column" id={ "footnote-" + item.index }>
                    <p className="has-small-font-size" id={ "fn:" + item.index }>
                    <sup>{ item.index }</sup> { item.text } <a href="{ item.url }" target="_blank" rel="noreferrer noopener">{ item.url }</a><a href={ "#fnref:"  + item.index } rev="footnote"><img draggable="false" role="img" className="emoji" alt="↩" src={ oet_footnotes.back_icon } /></a></p>
                </div>
            </div>))}
		</div>
	);
}
