/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from '@wordpress/i18n';

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-block-editor/#useBlockProps
 */
import { InnerBlocks, useBlockProps, InspectorControls } from '@wordpress/block-editor';
import { PanelBody, PanelRow, __experimentalNumberControl as NumberControl, TextControl } from '@wordpress/components';
import { Fragment, useState } from '@wordpress/element';

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import './editor.scss';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#edit
 *
 * @return {WPElement} Element to render.
 */
export default function Edit(props) {
    var display = __( 'Loading...', 'oet-footnotes-block' );
    const { attributes, setAttributes, clientId } = props;

    const noteList = () => {
        let arr = [];
        for (let i=0; i<attributes.footnoteCount; i++){
            arr.push(i);
        }
        return arr;
    }

    const notes = noteList();
    
    const defaultFootnote = {
        index: 0,
        text: '',
        url: '',
        anchor: ''
    };
    
    const setBlockId = ( blockId ) => {
        setAttributes( { blockId } );
    }

    const changeCount = ( count ) => {
        let footnotesCount = attributes.footnoteCount;
        let oFootnotes = attributes.footnotes;

        // Add default footnote object to footnotes array
        if (count>footnotesCount){
            oFootnotes.push(defaultFootnote);
        } else { // Remove last footnotes item array
            oFootnotes.splice(-1);
        }
        
        setAttributes({ footnoteCount: parseInt(count), footnotes: oFootnotes, isChanged: true });
    }

    const updateFootnotes = ( newVal, index, field ) => {
        let oFootnotes = attributes.footnotes;

        // Update Footnotes array        
        const updatedFootnotes = oFootnotes.map((val, id) => {
            if (id==(index-1)){
                switch(field){
                    case 'index':
                        val.index = parseInt(newVal);
                        break;
                    case 'text':
                        val.text = newVal;
                        break;
                    case 'url':
                        val.url = newVal;
                        break;
                    case 'anchor':
                        val.anchor = newVal;
                        break;
                }
                return val;
            } else {
                return val;
            }
        });

        setAttributes({ footnotes: updatedFootnotes, isChanged: true  })
    }

    const FootnoteControl = ({ index }) => {
        return (<Fragment key={ "footnote-group-" + index}>
                    <PanelBody
                        title = { __( 'Footnote ' + index  , 'oet-footnotes-block' ) }
                        key = { "footnote-panelbody-" + index }
                        >
                        <PanelRow className={ "footnote-control-group" } key = { "footnote-panelrow-index-" + index }>
                            <NumberControl
                                label = { __( 'Footnote #:' , 'oet-footnotes-block' ) }
                                isShiftStepEnabled = { true }
                                onChange = { e => updateFootnotes(e,index,'index') }
                                shiftStep = { 1 }
                                min = { 1 }
                                className = { "noteIndex" }
                                id = { "footnote-index-" + index }
                                key = { "footnote-index-" + index }
                                value = { attributes.footnotes[index-1].index } />
                        </PanelRow>
                        <PanelRow className={ "footnote-control-group" }  key = { "footnote-panelrow-text-" + index }>
                            <TextControl
                                label = { __( 'Text:' , 'oet-footnotes-block' ) }
                                value = { attributes.footnotes[index-1].text }
                                id = { "footnote-text-" + index }
                                key = { "footnote-text-" + index }
                                onChange = { e => updateFootnotes(e,index,'text') }
                                />
                        </PanelRow>
                        <PanelRow className={ "footnote-control-group" } key = { "footnote-panelrow-url-" + index }>
                            <TextControl
                                label = { __( 'URL:' , 'oet-footnotes-block' ) }
                                value = { attributes.footnotes[index-1].url }
                                id = { "footnote-url-" + index }
                                key = { "footnote-url-" + index }
                                onChange = { e => updateFootnotes(e,index,'url') }
                                />
                        </PanelRow>
                        <PanelRow className={ "footnote-control-group" } key = { "footnote-panelrow-anchor-" + index }>
                            <TextControl
                                label = { __( 'Anchor:' , 'oet-footnotes-block' ) }
                                value = { attributes.footnotes[index-1].anchor }
                                id = { "footnote-anchor-" + index }
                                key = { "footnote-anchor-" + index }
                                onChange = { e => updateFootnotes(e,index,'anchor') }
                                />
                        </PanelRow>
                    </PanelBody>
                </Fragment>);
    }

    const FootnoteControls = ( { count } ) => {
        let controls = [];  
        for (let i=1; i <= count; i++ ){
            controls.push(<FootnoteControl index={i} key={i} />);
        }
        return controls;
    }

    if ( clientId !== attributes.blockId )
        setBlockId(clientId);


    if (attributes.firstLoad){
        display = <hr className="wp-block-separator has-text-color has-background has-dark-gray-background-color has-dark-gray-color is-style-wide" />;
    } 

    if (attributes.footnoteCount>0){
        let footnotes = attributes.footnotes;
        let fControls = [];
        footnotes.map((item, key) => {
            if (item.index!=0 && item.text!=''){
                fControls.push(<div className="oet-footnote-block">
                            <div className="wp-block-column" id={ "footnote-" + item.index }>
                                <p className="has-small-font-size" id={ "fn:" + item.index }>
                                    <sup>{ item.index }</sup> { item.text } <a href={ item.url } target="_blank" rel="noreferrer noopener">{ item.url }</a><a href={ "#fnref:"  + item.index }  className="ret-anchor" rev="footnote"><img draggable="false" role="img" className="emoji" alt="↩" src={ oet_footnotes.back_icon } /></a></p>
                            </div>
                        </div>);
            }
        });
        display = fControls;
    }

    let inspector = <Fragment>
                        <InspectorControls key = "inspector-control">
                            <PanelBody
                                title = { __( 'Settings' , 'oet-footnotes-block' ) }
                                initialOpen = { true }>
                                <PanelRow>
                                    <NumberControl
                                        label = { __( 'Number of footnotes:' , 'oet-footnotes-block' ) }
                                        isShiftStepEnabled = { true }
                                        onChange = { changeCount }
                                        shiftStep = { 1 } 
                                        min = { 1 }
                                        className = { "footnotes-count" }
                                        key = { "footnotes-count" }
                                        value = { attributes.footnoteCount } />
                                </PanelRow>
                                {notes.map((item,key)=>{
                                    return (<Fragment key={ "footnote-group-" + (item+1) }>
                                        <PanelBody
                                            title = { __( 'Footnote ' + (item+1)  , 'oet-footnotes-block' ) }
                                            key = { "footnote-panelbody-1" }
                                            >
                                            <PanelRow className={ "footnote-control-group" } key = { "footnote-panelrow-index-" + (item+1) }>
                                                <NumberControl
                                                    label = { __( 'Footnote #:' , 'oet-footnotes-block' ) }
                                                    isShiftStepEnabled = { true }
                                                    onChange = { e => updateFootnotes(e,(item+1),'index') }
                                                    shiftStep = { 1 }
                                                    min = { 1 }
                                                    className = { "noteIndex" }
                                                    id = { "footnote-index-" + (item+1) }
                                                    key = { "footnote-index-" + (item+1) }
                                                    value = { attributes.footnotes[item].index } />
                                            </PanelRow>
                                            <PanelRow className={ "footnote-control-group" }  key = { "footnote-panelrow-text-" + (item+1) }>
                                                <TextControl
                                                    label = { __( 'Text:' , 'oet-footnotes-block' ) }
                                                    value = { attributes.footnotes[item].text }
                                                    id = { "footnote-text-" + (item+1) }
                                                    key = { "footnote-text-" + (item+1) }
                                                    onChange = { e => updateFootnotes(e,(item+1),'text') }
                                                    />
                                            </PanelRow>
                                            <PanelRow className={ "footnote-control-group" } key = { "footnote-panelrow-url-" + (item+1) }>
                                                <TextControl
                                                    label = { __( 'URL:' , 'oet-footnotes-block' ) }
                                                    value = { attributes.footnotes[item].url }
                                                    id = { "footnote-url-" + (item+1) }
                                                    key = { "footnote-url-" + (item+1) }
                                                    onChange = { e => updateFootnotes(e,(item+1),'url') }
                                                    />
                                            </PanelRow>
                                            <PanelRow className={ "footnote-control-group" } key = { "footnote-panelrow-anchor-" + (item+1) }>
                                                <TextControl
                                                    label = { __( 'Anchor:' , 'oet-footnotes-block' ) }
                                                    value = { attributes.footnotes[item].anchor }
                                                    id = { "footnote-anchor-" + (item+1) }
                                                    key = { "footnote-anchor-" + (item+1) }
                                                    onChange = { e => updateFootnotes(e,(item+1),'anchor') }
                                                    />
                                            </PanelRow>
                                        </PanelBody>
                                    </Fragment>)
                                })}
                            </PanelBody>
                        </InspectorControls>
                    </Fragment>;

	return (
		<div className="oet-footnotes-block" { ...useBlockProps() }>
            <hr className="wp-block-separator has-text-color has-background has-dark-gray-background-color has-dark-gray-color is-style-wide" />;
            { display }
            { inspector }
		</div>
	);
}
